
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { URL_SERVICIOS } from '../config/config/config.component';
import { Usuario } from '../model/usuario/usuario.model';
import { map} from 'rxjs/operators';

import {Observable, throwError} from 'rxjs';
import Swal from 'sweetalert2' ;


@Injectable()
export class UsuarioService {

//usuario:Usuario;
token:string;
usuario: Usuario;


  constructor(
    public http: HttpClient,
    public router: Router

  ) { 
    this.cargarStorage();
  }

  estaLogueado(){
    return (this.token.length>5) ? true:false;
  }

  cargarStorage() {

    if ( localStorage.getItem('token')) {
      this.token = localStorage.getItem('token');
      this.usuario= JSON.parse(localStorage.getItem('user'));
      console.log('paso po el cargastorage');
     
    } else {
     this.token = '';
     this.usuario = null;
      console.log('no paso');
    }

  }

  crearUsuario(usuario: Usuario){

    let url=  URL_SERVICIOS + '/usuario';
    return this.http.post(url,usuario);

  }
  
  guardarStorage( token: string, usuario: Usuario) {

    localStorage.setItem('token', token );
    localStorage.setItem('user', JSON.stringify(usuario) );

    //this.usuario = usuario;
    this.token = token;
  }
  
  logout(){

   
   this.usuario=null,
    this.token=''

    localStorage.removeItem('token');
    localStorage.removeItem('user');
    
  this.router.navigate(['/login']);
  console.log('se deslogueo');

  }

  login(usuario: Usuario): Observable<any>
  {
    let url=URL_SERVICIOS+'/login';
    return this.http.post(url, usuario).pipe(
          map((resp:any)=>{
            
            localStorage.setItem('token', resp.token );
            localStorage.setItem('user',JSON.stringify(resp.user));
            this.token = resp.token;
            this.usuario=resp.user;
            console.log(this.usuario);
            return true;
          })
    )
  
}

  cargarUsuarios(){
    let url=URL_SERVICIOS+'/usuario';
    console.log(this.usuario,this.token,'hola');
    return this.http.get(url);

  }

  eliminarUsuarios(usuarioId){
    let url=URL_SERVICIOS+'/usuario/'+usuarioId;
    return this.http.delete(url);
  }

  cargarNombre(){
    const userData = JSON.parse(localStorage.getItem('user'));
    return userData;
  }
  
}
