
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {Injectable} from '@angular/core';
import { Tramite } from 'src/app/model/tramite/tramite.model';
import { URL_SERVICIOS } from '../../config/config/config.component';

@Injectable({
    providedIn: 'root'
  })
  export class Area1Service {
  
  tramite:Tramite;

  constructor(
    public http:HttpClient
  ){}

    cargarArea1(){
      let url=URL_SERVICIOS + '/area1';
      return this.http.get(url);
    }

    //Upload file
    cargarArchivo(img:String){
      let url=URL_SERVICIOS + '/area1/' + 'img';
      return this.http.get(url);
    }

    //Download File
    public downloadFile(file,id:String){
      return this.http.post(URL_SERVICIOS + '/area1/' + id, file,{
        responseType : 'blob',
        headers : new HttpHeaders().append('content-type','application/json')
      });
    }

  }