import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { URL_SERVICIOS } from '../config/config/config.component';
import {Tramite} from '../model/tramite/tramite.model';

@Injectable({
  providedIn: 'root'
})
export class TramitesService {

  constructor(
    public http: HttpClient
  ) { }

 

  guardarTramites(destrino:Tramite ,archivo:File){
    return new Promise((resolve, reject)=>{
      let formData=new FormData();
          let xhr=new XMLHttpRequest();
          formData.append('nombreTramite', destrino.nombreTramite);
          formData.append('nombreUsuario', destrino.nombreUsuario);
          formData.append('apellidoUsuario', destrino.apellidoUsuario);
          formData.append('archivo2', archivo, archivo.name);
          
          formData.append('areaDestino', destrino.areaDestino);

          xhr.onreadystatechange=function(){
            if(xhr.readyState ===4 ){
                if(xhr.status===200){
                  console.log('imagen subida');
                  resolve(xhr.response);
                }else{
                  console.log('fallo la subida');
                  reject(xhr.response);
                }
            }
          };

      let url=URL_SERVICIOS+'/tramite';
      xhr.open('POST', url,true);
      xhr.send(formData);
    });

  }

  enlistaTramite(){
    let url=URL_SERVICIOS+'/tramitelistado';
    return this.http.get(url);
  }

}
