
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { URL_SERVICIOS } from '../config/config/config.component';
import { Tramite } from '../model/tramite/tramite.model';
import { Usuario } from '../model/usuario/usuario.model';


@Injectable({
  providedIn: 'root'
})
export class ObservadosService {

  constructor(
    public http: HttpClient,
    public router: Router

  ) { }

  actualizarEstado(id:number, body:any){
      let url=URL_SERVICIOS + '/area1/'+id;
      console.log('control 1');
      return this.http.put(url, body);
  }

  mostrarObservados(){
    let url=URL_SERVICIOS + '/observados';
    return this.http.get(url);
  }

}